.input-clean {
  color: rgb(199, 197, 197);
}

.input-valid {
  color: rgb(42, 151, 42);
}

.input-invalid {
  color: rgb(184, 46, 46);
}

.input-clean,
.input-valid,
.input-invalid {
  font-size: 17px;
}

.icon {
  cursor: pointer;
}
.mt-1 {
  margin-left: 700px !important;
}
th.ascending::after {
  content: " 	\0021C5";
  display: inline-block;
  margin-left: 1em;
}

th.descending::after {
  content: "\0021C5";
  display: inline-block;
  margin-left: 1em;
}
.ErrorMsg::before {
  display: inline;
  content: "âš  ";
}
.ErrorMsg {
  color: #bf1650;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

#root {
  height: 100%;
}

.tab-button-sm {
  font-size: 12px !important;
  min-width: 40px !important;
  width: 100px !important;
}

.custom-card {
  height: 80px;
  margin-bottom: 10px;
  background-color: #fafafa !important;
}

.node,
span[type^="supply-node"] {
  cursor: pointer;
  user-select: none;
  -webkit-text-stroke: 1px rgb(87, 87, 87);
}

.node {
  color: rgb(255, 255, 255);
}

.node:hover,
span[type^="supply-node"]:hover {
  -webkit-text-stroke: 2px rgb(87, 87, 87);
}

.supply-node-normal {
  color: rgb(21, 197, 21);
}

.supply-node-problem {
  color: rgb(218, 42, 42);
}

.node-text {
  color: rgb(44, 44, 44);
  -webkit-text-stroke: 0;
  font-weight: 500;
}

* {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}

.node-connector {
  background-color: rgb(131, 131, 131);
  width: 50px;
  height: 2px;
  margin-bottom: 3px;
  margin-left: -3px;
  margin-right: -3px;
  display: inline-block;
}

.lineage {
  margin-left: auto;
}

.list-container {
  display: flex;
  flex-direction: column;
}

.lineage-container {
  display: flex;
  padding: 15px 15px 15px 0;
  flex-direction: column;
  cursor: pointer;
}

.lineage-container:hover {
  background-color: #f5f5f5;
}

.text-title {
  margin-left: -10px;
  font-weight: 450;
}

.action-tag {
  cursor: pointer;
  color: #3f51b5;
}

.footer-container {
  padding: 20px 20px 20px 20px;
  background-color: white;
}

.spacer {
  width: 10px;
}

.flex-center {
  display: flex;
  align-items: center;
}

.full-width {
  width: 100%;
}

.spaced .children:not(:last-child) {
  margin-bottom: 35px;
}

fieldset {
  border: 0;
}

.icon {
  cursor: pointer;
}

.padding-1 {
  padding-top: 15px;
  padding-bottom: 15px;
}

.tooltip-message {
  font-size: 13px;
}

.editIcon {
  color: green;
}

.option-container {
  width: 150px;
}

.option {
  padding: 8px;
  display: flex;
  align-items: center;
  color: #757575;
  font-size: 18px !important;
}

.icon-text {
  padding-left: 15px;
}

.sub-option-container {
  width: 120px;
}

.option:hover {
  background: rgba(85, 84, 84, 0.144);
  color: rgb(57, 156, 57);
  cursor: pointer;
}

.entity-card {
  align-content: center;
  align-items: center;
  border-radius: 0px;
  padding-left: 10px;
  display: flex;
  margin: 5px;
}

.entity-remove {
  margin-left: auto !important;
}

.entity-remove span,
.entity-controls span {
  font-size: 20px !important;
}

tbody > tr:first-of-type {
  background-color: #edf5ff !important;
}
